import { createContext, useState } from 'react';

import Alert from './Common/Alert/Alert';
import Eror404 from './Pages/Eror404/Eror404';
import Footer from './Pages/Footer/Footer';
import Navbar from './Pages/Topbar/Navbar';
import WeatherPage from './Pages/Weather Page/WeatherPage';
import Loader from './Common/Loader/Loader';

import { Route, Routes } from 'react-router-dom';

export const sendLoader = createContext();

export default function App() {

  const [alert, setAlert] = useState(null);

  const showAlert = (message, type) => {

    setAlert({
      message: message,
      type: type

    });

    setTimeout(() => {
      setAlert(null);
    }, 4000)
  }

  return (

    <div className="g-0">
      <div className="container-md">

        <sendLoader.Provider value={Loader}>
          <div className="row">
            <Navbar />
            <div className="g-0">
              <div className="px-2">

                <Alert alert={alert} />

                <Routes>
                  <Route path='*' element={<Eror404 />} />
                  <Route path='/' element={<WeatherPage showAlert={showAlert} />} />
                  <Route path='/weatherhub' element={<WeatherPage showAlert={showAlert} />} />
                </Routes>

              </div>

              <Footer />

            </div>
          </div>
        </sendLoader.Provider>
      </div>
    </div>

  );
}